.my-resume {
  background-color: var(--background-gray);
  color: white;
  padding-inline: 10%;
  padding-block: 5%;
}
.my-resume .my-resume-heading {
  text-align: center;
}
.my-resume .my-resume-heading span {
  color: var(--accent-color);
}
.my-resume .my-resume-card {
  background-color: var(--background-lightgray);
  padding: 5%;
  margin-top: 3%;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.my-resume .my-resume-card .my-resume-card-block {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.my-resume .my-resume-card .my-resume-card-block .my-resume-card-block-item {
  display: flex;
  align-items: start;
  gap: 20px;
}
.my-resume .my-resume-card .my-resume-card-block .my-resume-card-block-item .my-resume-card-block-item-icon {
  background-color: var(--accent-color);
  border-radius: 50px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.my-resume .my-resume-card .my-resume-card-block .my-resume-card-block-item .my-resume-card-block-item-text h5, .my-resume .my-resume-card .my-resume-card-block .my-resume-card-block-item .my-resume-card-block-item-text h6 {
  color: gray;
}
.my-resume .my-resume-card .my-resume-card-divider {
  width: 1px;
  background-color: gray;
}
.my-resume .my-resume-btn {
  margin-top: 5%;
  display: flex;
  justify-content: center;
  color: black;
}

@media only screen and (max-width: 600px) {
  .my-resume-card {
    gap: 40px;
    margin-top: 10% !important;
  }
  .my-resume-card-block {
    width: 100%;
  }
  .my-resume-card-divider {
    display: none;
  }
  .my-resume-btn {
    margin-top: 10% !important;
  }
}/*# sourceMappingURL=ResumeSection.css.map */